import React, { useState, useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Container,
  Typography,
  Box,
  Paper,
  TextField,
  Button,
  IconButton,
  CircularProgress,
  Alert,
  Divider,
  Card,
  CardContent,
  Chip,
  Tooltip,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions
} from '@mui/material';
import {
  Mic as MicIcon,
  MicOff as MicOffIcon,
  Send as SendIcon,
  VolumeUp as SpeakIcon,
  VolumeOff as MuteIcon,
  ExitToApp as ExitIcon,
  Help as HelpIcon
} from '@mui/icons-material';
import api from '../services/api';

// 
const useRecorder = () => {
  const [isRecording, setIsRecording] = useState(false);
  const [audioBlob, setAudioBlob] = useState(null);
  const mediaRecorderRef = useRef(null);
  const chunksRef = useRef([]);

  const startRecording = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      mediaRecorderRef.current = new MediaRecorder(stream);
      
      mediaRecorderRef.current.ondataavailable = (e) => {
        if (e.data.size > 0) {
          chunksRef.current.push(e.data);
        }
      };
      
      mediaRecorderRef.current.onstop = () => {
        const blob = new Blob(chunksRef.current, { type: 'audio/wav' });
        setAudioBlob(blob);
        chunksRef.current = [];
      };
      
      mediaRecorderRef.current.start();
      setIsRecording(true);
    } catch (err) {
      console.error(':', err);
      alert('');
    }
  };

  const stopRecording = () => {
    if (mediaRecorderRef.current && isRecording) {
      mediaRecorderRef.current.stop();
      setIsRecording(false);
      
      // 
      mediaRecorderRef.current.stream.getTracks().forEach(track => track.stop());
    }
  };

  return { isRecording, audioBlob, startRecording, stopRecording };
};

const Interview = () => {
  const navigate = useNavigate();
  const [session, setSession] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [currentQuestion, setCurrentQuestion] = useState(null);
  const [answer, setAnswer] = useState('');
  const [questions, setQuestions] = useState([]);
  const [answers, setAnswers] = useState([]);
  const [isPlaying, setIsPlaying] = useState(false);
  const [exitDialogOpen, setExitDialogOpen] = useState(false);
  const audioRef = useRef(null);
  
  // 
  const { isRecording, audioBlob, startRecording, stopRecording } = useRecorder();

  // 
  useEffect(() => {
    const sessionData = localStorage.getItem('interviewSession');
    if (!sessionData) {
      navigate('/setup');
      return;
    }
    
    try {
      const parsedSession = JSON.parse(sessionData);
      setSession(parsedSession);
      // 
      fetchQuestion(parsedSession.interview_id);
    } catch (err) {
      console.error(':', err);
      navigate('/setup');
    }
  }, [navigate]);

  // 
  const fetchQuestion = async (interviewId) => {
    setLoading(true);
    setError('');

    try {
      const response = await api.get(`/api/interview/question?interview_id=${interviewId}`);
      
      if (response.data.status === 'success') {
        const question = response.data.data;
        setCurrentQuestion(question);
        setQuestions([...questions, question]);
        
        // 
        if (question.content) {
          playQuestionAudio(question.content);
        }
      } else {
        setError(response.data.message || '');
      }
    } catch (err) {
      console.error(':', err);
      setError('');
    } finally {
      setLoading(false);
    }
  };

  // 
  const playQuestionAudio = async (text) => {
    try {
      console.log("TTS API..."); const response = await api.post('/api/speech/tts', {
        text,
        language: session?.language || 'zh'
      });
      
      if (response.data.status === 'success') {
        const audioData = response.data.data.audio;
        const audioSrc = `data:audio/wav;base64,${audioData}`;
        
        if (audioRef.current) {
          audioRef.current.src = audioSrc;
          audioRef.current.play();
          setIsPlaying(true);
        }
      }
    } catch (err) {
      console.error(':', err);
    }
  };

  // 
  const submitAnswer = async () => {
    if (!answer.trim() || !currentQuestion || !session) return;
    
    setLoading(true);
    setError('');

    try {
      console.log("TTS API..."); const response = await api.post('/api/interview/answer', {
        interview_id: session.interview_id,
        question_id: currentQuestion.id,
        answer: answer
      });
      
      if (response.data.status === 'success') {
        const analysis = response.data.data;
        
        // 
        const newAnswer = {
          question: currentQuestion,
          content: answer,
          analysis
        };
        
        setAnswers([...answers, newAnswer]);
        setAnswer('');
        
        // 
        if (analysis.next_question) {
          // 
          fetchQuestion(session.interview_id);
        } else {
          // 
          localStorage.setItem('interviewAnswers', JSON.stringify([...answers, newAnswer]));
          navigate('/results');
        }
      } else {
        setError(response.data.message || '');
      }
    } catch (err) {
      console.error(':', err);
      setError('');
    } finally {
      setLoading(false);
    }
  };

  // 
  const processAudioRecording = async () => {
    if (!audioBlob) return;
    
    setLoading(true);
    
    try {
      const formData = new FormData();
      formData.append('audio', audioBlob);
      formData.append('language', session?.language || 'zh');
      
      console.log("TTS API..."); const response = await api.post('/api/speech/asr', formData);
      
      if (response.data.status === 'success') {
        const recognizedText = response.data.data.text;
        setAnswer(prev => prev + ' ' + recognizedText);
      }
    } catch (err) {
      console.error(':', err);
    } finally {
      setLoading(false);
    }
  };

  // 
  useEffect(() => {
    if (audioBlob && !isRecording) {
      processAudioRecording();
    }
  }, [audioBlob, isRecording]);

  // 
  useEffect(() => {
    const audioElement = audioRef.current;
    
    const handleEnded = () => {
      setIsPlaying(false);
    };
    
    if (audioElement) {
      audioElement.addEventListener('ended', handleEnded);
    }
    
    return () => {
      if (audioElement) {
        audioElement.removeEventListener('ended', handleEnded);
      }
    };
  }, []);

  // 
  const handleExit = () => {
    setExitDialogOpen(true);
  };

  const confirmExit = () => {
    localStorage.removeItem('interviewSession');
    navigate('/');
  };

  return (
    <Container maxWidth="md">
      <Box sx={{ my: 4 }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
          <Typography variant="h4" component="h1">
            AI
          </Typography>
          <Tooltip title="">
            <IconButton color="error" onClick={handleExit}>
              <ExitIcon />
            </IconButton>
          </Tooltip>
        </Box>

        {error && (
          <Alert severity="error" sx={{ mb: 3 }}>
            {error}
          </Alert>
        )}

        <Paper elevation={3} sx={{ p: 3, mb: 3 }}>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
            <Typography variant="h6">
              {session?.type && interviewTypes[session.type]} 
            </Typography>
            <Chip 
              label={session?.company || ''} 
              color="primary" 
              variant="outlined" 
            />
          </Box>
          
          <Divider sx={{ mb: 2 }} />
          
          <Box sx={{ minHeight: '100px', display: 'flex', alignItems: 'center' }}>
            {loading && !currentQuestion ? (
              <Box sx={{ display: 'flex', justifyContent: 'center', width: '100%' }}>
                <CircularProgress />
              </Box>
            ) : currentQuestion ? (
              <Box>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                  <Typography variant="subtitle1" color="text.secondary">
                     {questions.length}:
                  </Typography>
                  <IconButton 
                    size="small" 
                    onClick={() => playQuestionAudio(currentQuestion.content)}
                    disabled={isPlaying}
                    sx={{ ml: 1 }}
                  >
                    {isPlaying ? <MuteIcon fontSize="small" /> : <SpeakIcon fontSize="small" />}
                  </IconButton>
                </Box>
                <Typography variant="h6">{currentQuestion.content}</Typography>
              </Box>
            ) : (
              <Typography variant="body1" color="text.secondary">
                ...
              </Typography>
            )}
          </Box>
        </Paper>

        <Card sx={{ mb: 3 }}>
          <CardContent>
            <Typography variant="subtitle1" gutterBottom>
              :
            </Typography>
            <TextField
              fullWidth
              multiline
              rows={6}
              value={answer}
              onChange={(e) => setAnswer(e.target.value)}
              placeholder="..."
              disabled={loading || !currentQuestion}
              sx={{ mb: 2 }}
            />
            
            <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
              <Box>
                <Tooltip title={isRecording ? "" : ""}>
                  <IconButton 
                    color={isRecording ? "error" : "primary"}
                    onClick={isRecording ? stopRecording : startRecording}
                    disabled={loading || !currentQuestion}
                  >
                    {isRecording ? <MicOffIcon /> : <MicIcon />}
                  </IconButton>
                </Tooltip>
                {isRecording && (
                  <Typography variant="caption" color="error" component="span" sx={{ ml: 1 }}>
                    ...
                  </Typography>
                )}
              </Box>
              
              <Button
                variant="contained"
                endIcon={<SendIcon />}
                onClick={submitAnswer}
                disabled={loading || !answer.trim() || !currentQuestion}
              >
                {loading ? '...' : ''}
              </Button>
            </Box>
          </CardContent>
        </Card>

        {answers.length > 0 && (
          <Paper elevation={2} sx={{ p: 3 }}>
            <Typography variant="h6" gutterBottom>
              :
            </Typography>
            <Box sx={{ maxHeight: '200px', overflowY: 'auto' }}>
              {answers.map((item, index) => (
                <Box key={index} sx={{ mb: 2 }}>
                  <Typography variant="subtitle2" color="text.secondary">
                     {index + 1}: {item.question.content}
                  </Typography>
                  <Typography variant="body2" sx={{ ml: 2 }}>
                    {item.content}
                  </Typography>
                  <Divider sx={{ my: 1 }} />
                </Box>
              ))}
            </Box>
          </Paper>
        )}
      </Box>

      {/*  */}
      <audio ref={audioRef} style={{ display: 'none' }} />

      {/*  */}
      <Dialog
        open={exitDialogOpen}
        onClose={() => setExitDialogOpen(false)}
      >
        <DialogTitle>?</DialogTitle>
        <DialogContent>
          <DialogContentText>
            
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setExitDialogOpen(false)}></Button>
          <Button onClick={confirmExit} color="error">
            
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

// 
const interviewTypes = {
  'software_engineer': '',
  'product_manager': '',
  'data_scientist': '',
  'frontend_developer': '',
  'backend_developer': ''
};

export default Interview; 