import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Container,
  Typography,
  Box,
  Paper,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  FormControlLabel,
  Switch,
  Button,
  Grid,
  CircularProgress,
  Alert,
  Divider
} from '@mui/material';
import {
  ArrowBack as BackIcon,
  PlayArrow as StartIcon,
  Settings as SettingsIcon
} from '@mui/icons-material';
import axios from 'axios';

const Setup = () => {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [interviewTypes, setInterviewTypes] = useState({});
  
  // 
  const [formData, setFormData] = useState({
    type: 'software_engineer',
    company: '',
    language: 'zh',
    useML: true
  });

  // 
  useEffect(() => {
    const fetchInterviewTypes = async () => {
      try {
        const response = await axios.get('/api/interview/types');
        if (response.data.status === 'success') {
          setInterviewTypes(response.data.data);
        }
      } catch (err) {
        console.error(':', err);
        // 
        setInterviewTypes({
          'software_engineer': '',
          'product_manager': '',
          'data_scientist': '',
          'frontend_developer': '',
          'backend_developer': ''
        });
      }
    };

    fetchInterviewTypes();
  }, []);

  // 
  const handleChange = (e) => {
    const { name, value, checked } = e.target;
    setFormData({
      ...formData,
      [name]: name === 'useML' ? checked : value
    });
  };

  // 
  const handleStartInterview = async () => {
    setLoading(true);
    setError('');

    try {
      const response = await axios.post('/api/interview/start', {
        type: formData.type,
        company: formData.company || '',
        language: formData.language,
        use_ml: formData.useML
      });

      if (response.data.status === 'success') {
        // 
        localStorage.setItem('interviewSession', JSON.stringify(response.data.data));
        // 
        navigate('/interview');
      } else {
        setError(response.data.message || '');
      }
    } catch (err) {
      console.error(':', err);
      setError('');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Container maxWidth="md">
      <Box sx={{ my: 4 }}>
        <Button
          startIcon={<BackIcon />}
          onClick={() => navigate('/')}
          sx={{ mb: 2 }}
        >
          
        </Button>

        <Paper elevation={3} sx={{ p: 4 }}>
          <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
            <SettingsIcon color="primary" sx={{ fontSize: 30, mr: 2 }} />
            <Typography variant="h4" component="h1">
              
            </Typography>
          </Box>

          <Divider sx={{ mb: 4 }} />

          {error && (
            <Alert severity="error" sx={{ mb: 3 }}>
              {error}
            </Alert>
          )}

          <Grid container spacing={3}>
            <Grid item xs={12} sm={6}>
              <FormControl fullWidth>
                <InputLabel id="interview-type-label"></InputLabel>
                <Select
                  labelId="interview-type-label"
                  name="type"
                  value={formData.type}
                  onChange={handleChange}
                  label=""
                >
                  {Object.entries(interviewTypes).map(([key, value]) => (
                    <MenuItem key={key} value={key}>
                      {value}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>

            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                label=""
                name="company"
                value={formData.company}
                onChange={handleChange}
                placeholder=""
                helperText=""
              />
            </Grid>

            <Grid item xs={12} sm={6}>
              <FormControl fullWidth>
                <InputLabel id="language-label"></InputLabel>
                <Select
                  labelId="language-label"
                  name="language"
                  value={formData.language}
                  onChange={handleChange}
                  label=""
                >
                  <MenuItem value="zh"></MenuItem>
                  <MenuItem value="en"></MenuItem>
                </Select>
              </FormControl>
            </Grid>

            <Grid item xs={12} sm={6}>
              <FormControlLabel
                control={
                  <Switch
                    checked={formData.useML}
                    onChange={handleChange}
                    name="useML"
                    color="primary"
                  />
                }
                label=""
              />
              <Typography variant="caption" color="text.secondary" display="block">
                
              </Typography>
            </Grid>
          </Grid>

          <Box sx={{ mt: 4, textAlign: 'center' }}>
            <Button
              variant="contained"
              size="large"
              startIcon={loading ? <CircularProgress size={20} color="inherit" /> : <StartIcon />}
              onClick={handleStartInterview}
              disabled={loading}
              sx={{ py: 1.5, px: 4, fontSize: '1.1rem' }}
            >
              {loading ? '...' : ''}
            </Button>
          </Box>
        </Paper>
      </Box>
    </Container>
  );
};

export default Setup; 